.spinner {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden;

    width: 75px;
    height: 75px;
    border-radius: 50%;
    
    border-top: 8px solid rgb(0, 0, 0, 0.3);
    border-left: 8px solid rgb(0, 0, 0, 0.3);
    border-bottom: 8px solid rgb(0, 0, 0, 0.3);
    border-right: 8px solid #ddd;


    animation-name: spin;
    animation-duration: 0.75s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;

    &--large {
        top: 50%;

        width: 150px;
        height: 150px;

        border-top: 12px solid rgb(0, 0, 0, 0.3);
        border-left: 12px solid rgb(0, 0, 0, 0.3);
        border-bottom: 12px solid rgb(0, 0, 0, 0.3);
        border-right: 12px solid #ddd;
    }
}

.is-hidden {
    display: none;
}

@keyframes spin {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
    }

    100% {
        transform: translate(-50%, -50%) rotate(360deg);
    }
}