.search {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 100px;

  background-image: repeating-linear-gradient(
    to bottom,
    rgb(0, 113, 179),
    rgb(0, 113, 179) 20px,
    rgb(0, 123, 194) 20px,
    rgb(0, 123, 194) 40px
  );
}

.search-form {
  position: relative;
  display: flex;
  width: 350px;
  height: 35px;

  border-radius: 10px;
}

.search-form__input {
  width: 100%;
  height: 35px;
  padding-left: 10px;

  font-size: 20px;

  border-radius: 10px;
  border: none;

  &:focus {
    outline: 2px solid #FFA800;
  }
}

.search-form__button {
  display: block;
  position: absolute;
  right: 0;

  height: 100%;
  width: 100px;
  border: none;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  margin: 0; 
  padding: 0;

    &:hover, 
    &:focus {   
    color: #fff;
    background-color: #23558F;
    outline: none;
  }
}
